<template>
  <div id="app">
    <header>
      <menucustom
        @logout="logoutUser"
        :username="username"
        :logged="logged"
        :role="role"
      />
    </header>
    <main>
      <router-view
        @login="doLogin"
        @courses="setCourses"
        :courses="courses"
        :userCourses="userCourses"
        :logged="logged"
      />
    </main>
    <footer>
      <footercustom />
    </footer>
  </div>
</template>

<script>
import menucustom from "@/components/MenuCustom.vue";
import footercustom from "@/components/FooterCustom.vue";
export default {
  name: "App",
  components: {
    menucustom,
    footercustom,
  },
  data() {
    return {
      username: "",
      logged: false,
      role: "",
      courses: [],
      userCourses: []
    };
  },
  methods: {
    doLogin(result) {
      this.username = result.username;
      this.role = result.roles[0];
      this.userCourses = result.courses;
      this.logged = true;
    },
    setCourses(result) {
      this.courses = result;
    },
    logoutUser() {
      this.username = "";
      this.role = "";
      this.logged = false;
      localStorage.removeItem("jwt");
      localStorage.removeItem("name");
      localStorage.removeItem("role");
      localStorage.removeItem("courses");
    },
  },
  created() {
    this.username = localStorage.getItem("name");
    if (this.username) {
      this.logged = true;
    }
    this.role = localStorage.getItem("role");
  },
};
</script>

