#app {
  display: flex;
  flex-direction: column;
}

header {
  height: 15vh;
}

main {
  min-height: 75vh;
  display: flex;
}

footer {
  height: 10vh;
}

div.waiting {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: lightskyblue;
  display: grid;
  place-content: center;
}

