<template>
  <div class="container">
    <div class="search-container">
      <h1>Welcome to your Courses site!!!!!</h1>
      <h3>Find your course</h3>
      <form>
        <div class="row">
          <div class="col">
            <label for="language">Language</label>
            <select
              id="language"
              class="form-control form-control-sm"
              v-model="language"
            >
              <option>Java</option>
              <option>Python</option>
              <option>C</option>
              <option>Javascript</option>
              <option>Ruby</option>
            </select>
          </div>
          <div class="col">
            <label for="price" class="mr-sm-2">Max price</label>
            <select
              id="price"
              class="form-control form-control-sm"
              v-model="price"
            >
              <option>1000</option>
              <option>800</option>
              <option>600</option>
              <option>400</option>
              <option>200</option>
              <option>100</option>
            </select>
          </div>
        </div>
      </form>
      <button class="btn btn-success" @click="setParameters()">Search</button>
    </div>
    <div class="table-responsive-sm">
      <div v-if="!courses.length">
        No se han encontrado resultados para la búsqueda solicitada
      </div>
      <div v-else>
        <h3>Courses</h3>
        <div v-if="errorMessage" class="alert alert-danger alert-dismissable">
        <button class="close" data-dismiss="alert" @click="errorMessage=''">
            &times;
          </button>
        {{errorMessage}}
      </div>
      <div v-else-if="successMessage" class="alert alert-success">
        {{successMessage}}
      </div>
        <table class="table table-striped">
          <thead>
            <tr>
              <th>Id</th>
              <th>Description</th>
              <th>Language</th>
              <th>Format</th>
              <th>Price</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="course in courses" :key="course.id">
              <td>{{ course.id }}</td>
              <td>{{ course.description }}</td>
              <td>{{ course.language }}</td>
              <td>{{ course.format }}</td>
              <td>{{ course.price }}</td>
              <td v-if="isAdmin">
                <button
                  class="btn btn-success"
                  @click="updateCourse(course.id)"
                >
                  <pencilicon />
                </button>
                <button class="btn btn-danger" @click="deleteCourse(course.id)">
                  <trashicon />
                </button>
              </td>
              <td v-else>
                <button class="btn btn-success" :class="['btn', userCourses.includes(course) ? 'btn-danger' : 'btn-success']" @click="buyCourse(course.id)" >
                  Request
                </button>
              </td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import UserDataService from "../service/UserDataService";
import CourseDataService from "../service/CourseDataService";
import pencilicon from "@/components/PencilIcon";
import trashicon from "@/components/TrashIcon";
export default {
  name: "Home",
  components: {
    pencilicon,
    trashicon,
  },
  data() {
    return {
      language: "",
      format: "",
      price: 0,
      errorMessage: "",
      successMessage: "",
      courses: [],
    };
  },
  props: {
    logged: Boolean,
    userCourses: Array
  },
  computed: {
    isAdmin() {
      return localStorage.getItem("role") === "ROLE_ADMIN";
    },
    isRequested() {
      return this.userCourses.includes(course)
    }
    
  },

  methods: {
    setParameters() {
      let parameters = "";
      let count = 0;

      if (this.language) {
        parameters += "?language=" + this.language;
        count++;
      }

      if (this.price) {
        if (count === 0) parameters += "?price=" + this.price;
        else parameters += "&price=" + this.price;
        count++;
      }

      this.getCoursesList(parameters);
    },
    async getCoursesList(parameters) {
      try {
        const response = await CourseDataService.retrieveFilteredCourses(
          parameters
        );
        this.courses = response.data;
        localStorage.setItem("courses", JSON.stringify(this.courses));
      } catch (error) {
        this.errorMessage = "Couldn't get data, try it again later";
      }
    },
    updateCourse(id) {
      this.$router.push({ name: "UpdateCourse", params: { id: `${id}` } });
    },

    closeMessage() {
      setTimeout(() => {
        this.successMessage = "";
      }, 2000);
    },

    async deleteCourse(id) {
      try {
        await CourseDataService.deleteCourse(id);
        const newList = [...this.coursesList];
        this.coursesList = newList.filter((course) => course.id !== id);
        localStorage.setItem("courses", JSON.stringify(this.coursesList));
        this.successMessage = `Delete of course ${id} Successful`;
        this.closeMessage();
      } catch (error) {
        this.errorMessage = "Couldn't delete this course, try it again later";
      }
    },

    async buyCourse(id) {
      if (!this.logged) {
        this.$router.push({ name: "Login" });
        return;
      }
      const result = await CourseDataService.retrieveCourse(id);
      const course = result.data;
      const userId = localStorage.getItem("userId");
      const res = await UserDataService.addCourseToUser(userId, course);

      if (res.data === "ok") {
        this.successMessage = "Request Successful";
        setTimeout(() => {
          this.successMessage = "";
        }, 2000);
      }
    },

  },
  created() {
    const data = JSON.parse(localStorage.getItem("courses"));
    if(data) this.courses = data 
    else this.getCoursesList()
    console.log('userCourses: ',this.userCourses);
    // const isInclude = (e) => {
    //   this.courses.includes(e)
    // }
    const isInclude = this.courses.filter(e => {
      
      this.userCourses.includes(e)
        console.log('curso: ',e);
        return e
      
    })
    console.log('IncludedCourses: ',isInclude);
  },
};
</script>

<style scoped>
.search-container > button {
  margin: 25px 0;
}

button {
  display: inline-flex;
  align-items: center;
  margin-right: 5px;
}
</style>
